import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex);
export default new Vuex.Store({
	state:{
		name:"jack",
	},
	actions:{
		getName:function(){
			var me=this;
			setTimeout(function(){
				me.state.name="hhhh"
			},2000);
		}
	},
	mutations:{
		add(state){
			state.name="hhh";
		}
	}
});


