import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Home from '@/components/Home'
import pluginList from '@/components/pluginlist'
import pluginDown from '@/components/pluginlist'
import tree from '@/components/pluginlist/tree'
import carousel from '@/components/pluginlist/carousel'
import magn from '@/components/pluginlist/magn'
import Root from '@/components/root'
Vue.use(Router);
export default new Router({
  mode: 'history',
  routes: [{
    path: '/',
    redirect: '/home',
    meta: { index: 0 },
    component: Root,
    children: [
      { path: 'home', component: Home, meta: { index: 0 } },
      { path: 'pluginDown', component: pluginDown, meta: { index: 1 } },
      {
        path: 'pluginList',
        component: pluginList,
        meta: { index: 2 },
        children: [
          { path: 'tree', component: tree, meta: { index: 10 } },
          { path: 'carousel', component: carousel, meta: { index: 11 } },
          { path: 'magn', component: magn, meta: { index: 12 } }
        ]
      }
    ]
  }]
})



