<template>
  <div>
    <div class="plugin-list el-plugin-list" >
      <ul class="plugin-types" style="width:8rem!important" v-show="show">
        <li v-for="i in pluginList" :class="{'checked': i.active}" @click="SHow" >
          <router-link class="plugin-kind" :to="i.path" style="color:#000">
            {{i.name}}
          </router-link>
        </li>
      </ul>
      <div class="plugin-kind-list">
        <router-view>
        </router-view>
      </div>
      <div class="clear"></div>
    </div>
  </div>
</template>
<script>
export default {
  name: "pluginList",
  data: () => {
    return {
      route_height: '',
      pluginList: [{
          name: "插件使用说明",
          path: "pluginList/instruction",
          active: true
        }, {
          name: "图表",
          path: "pluginList/chart",
          active: false
        }, {
          name: "缓冲动画",
          path: "/pluginList/buffering",
          active: false
        }, {
          name: "轮播图",
          path: "/pluginList/carousel",
          active: false
        }, {
          name: "放大镜",
          path: "/pluginList/magn",
          active: false
        },{
          name: "分页",
          path: "/pluginList/paging",
          active: false
        }, {
          name: "选择框",
          path: "/pluginList/checkBox",
          active: false
        }, {
          name: "日期选择",
          path: "/pluginList/dateInput",
          active: false
        }, {
          name: "折叠",
          path: "/pluginList/foldCollapse",
          active: false
        }, {
          name: "图片放大",
          path: "/pluginList/imgShow",
          active: false
        }, {
          name: "自动补全",
          path: "/pluginList/inputAuto",
          active: false
        }, {
          name: "进度条",
          path: "/pluginList/progress",
          active: false
        }, {
          name: "开关",
          path: "/pluginList/switcher",
          active: false
        }, {
          name: "地址",
          path: "/pluginList/address",
          active: false
        }, {
          name: "表格",
          path: "/pluginList/table",
          active: false
        }, {
          name: "菜单树",
          path: "/pluginList/tree",
          active: false
        }
        // , {
        //     name: "颜色选择",
        //     path: "/route/pluginList/colorPicker",
        //     active: false
        // }
      ],
      show: true
    }
  },
  methods: {
    SHow() {
      console.log(1111);
      var me = this;
      me.show = !me.show;
    }
  },
  beforeMount() {},
  mounted() {
    // alert(11111);
  },
}

</script>



