  font-size: 26px;
}

.fore-red {
  color: #d02200;
}

.fore-blue {
  color: #409EFF;
}

.fore-shallow-green {
  color: #EFF9EB;
}

.fore-green {
  color: #67c23a;
}

.fore-black {
  color: #000;
}

.fore333 {
  color: #333;
}

.fore666 {
  color: #666;
}

.fore999 {
  color: #999;
}

