}

@media screen and (max-width: 800px) {
  .con-header {
    padding: 1rem !important;
  }

  .con-header ul {
    padding-top: 0.6rem;
    margin-right: 2rem;
  }

  .con-header .app {
    position: absolute;
    display: block !important;
    right: 1.8rem;
    top: 1.3rem;
    z-index: 9999;
